.app_wrapper {
    display: grid;
    width: 1200px;

    margin: 0 auto;

    grid-template-areas:
    "header header header"
    "nav content content";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 3px;

}
.app_wrapper_content{
    grid-area: content;
    background: #5bdeef;
}
